        <div v-for="count in resultCount" :key="count.name">
          <template v-if="count.count!=0">
            ÂÖ±Ë®à <strong class="text-decoration-underline">{{ count.count }}</strong> {{ count.unit }}{{ count.name }}
          </template>
        </div>
      </div>
    </div>
    
    <div class="custom-table-container">
      <div class="pagination-container">
        <el-pagination
          :current-page="pageDetial.page"
          :page-size="pageDetial.size"
          :page-sizes="[10, 50, 100]"
          :total="pageDetial.total"
          background
          layout="total, sizes, prev, pager, next"
          @size-change="handlePageSize"
          @current-change="handlePageChange"
        />
        </div>
      <!-- Ë£ÅÂà§Ë≥áË®ä Table -->
        <table class="table table-bordered custom-adjust-table">
          <thead>
            <tr class="text-center">
              <th class="custom-blue">Â∫èËôü</th>
              <template v-for="field in searchFields" :key="field.name">
                <th class="custom-blue" v-if="checkQueryEnable(field.name)">{{ field.type }}</th>
              </template>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item, index) in searchResults" :key="index">
              <td style="text-align: center;">{{ index + (pageDetial.page-1)*pageDetial.size + 1 }}</td>
              <template v-for="field in searchFields" :key="field.name" >
                <td v-if="checkQueryEnable(field.name)" :style="getColumnWidth(field.name)">
                  <template v-if="field.name == 'case_num'">
                    <a :href="item['jud_url']" target="_blank">{{ item['case_num'] }}</a>
                  </template>
                  <template v-else-if="field.name == 'jud_date'">
                    {{ formatDate(item['jud_date']) }}
                  </template>
                  <template v-else>
                    <p style="color:rgb(138, 138, 138);" v-if="item[field.name] == null">ÁÑ°</p>
                    <p v-else-if="(item[field.name].length > maxTextLength || (item[field.name].match(/\n/g) || []).length > 5)" class="mytooltip custom-overflow-column" @click="openDialog(addHighlighter(field.name, item[field.name]))">
                      <span  v-html="addHighlighter(field.name, item[field.name].substr(0,250))"></span>...more
                      <span class="tooltiptext">ÈªûÊìäÈñ±ËÆÄÂÖ®Êñá</span>
                    </p>
                    <p v-else v-html="addHighlighter(field.name, item[field.name])"></p>
                  </template>
              </td>
              </template>
            </tr>
            <tr v-if="searchResults.length == 0">
              <td class="no-found-cell" colspan="8">Êü•ÁÑ°Ë≥áÊñô
              </td></tr>
          </tbody>
        </table>
      <el-dialog
        v-model="dialogVisible"
        width="60%"
        style="max-height: 80vh; min-width: 200px;overflow: auto;"
        :before-close="handleClose"
      >
        <span v-html="dialogText"></span>
      </el-dialog>

      <div class="pagination-container">
        <el-pagination
          :current-page="pageDetial.page"
          :page-size="pageDetial.size"
          :page-sizes="[10, 50, 100]"
          :total="pageDetial.total"
          background
          layout="total, sizes, prev, pager, next"
          @size-change="handlePageSize"
          @current-change="handlePageChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// import testSearchResults from '../../data/api_search.json'

export default {
  data() {
    return {
      loading: true,
      mode: 'default', // default, sentence_kind
      params: {},
      searchFields: {
        court_type: {type: 'Ê≥ïÈô¢', name: 'court_type'},
        case_num: {type: 'Ê°àËôü', name: 'case_num'},
        jud_date: {type: 'Êó•Êúü', name: 'jud_date'},
        case_type: {type: 'Ê°à‰ª∂Âà•', name:'case_type'},
        basic_info: {type: 'Áï∂‰∫ã‰∫∫Á≠âÂü∫Êú¨Ë≥áÊñô', name:'basic_info'},
        opinion: {type: 'Ë¶ãËß£', name:'opinion'},
        fee: {type: 'ÂøÉË≠â', name:'fee'},
        sub: {type: 'Ê∂µÊîù', name:'sub'},
        jud_full: {type: 'ÂÖ®ÊñáÈóúÈçµÂ≠ó', name:'jud_full'},
      },
      courtTypeOptions: [
        { name: 'ÊúÄÈ´òÊ≥ïÈô¢', value: 'zgf' },
        { name: 'Ëá∫ÁÅ£È´òÁ≠âÊ≥ïÈô¢', value: 'twgdfy' },
        { name: 'Êô∫ÊÖßË≤°Áî¢ÂèäÂïÜÊ•≠Ê≥ïÈô¢', value: 'zhccjsyfy' },
        { name: 'Ëá∫ÁÅ£È´òÁ≠âÊ≥ïÈô¢Ëá∫‰∏≠ÂàÜÈô¢', value: 'twgdfytcfy' },
        { name: 'Ëá∫ÁÅ£È´òÁ≠âÊ≥ïÈô¢Ëá∫ÂçóÂàÜÈô¢', value: 'twgdfytnfy' },
        { name: 'Ëá∫ÁÅ£È´òÁ≠âÊ≥ïÈô¢È´òÈõÑÂàÜÈô¢', value: 'twgdfykxfy' },
        { name: 'Ëá∫ÁÅ£È´òÁ≠âÊ≥ïÈô¢Ëä±ËìÆÂàÜÈô¢', value: 'twgdfyhlfy' },
        { name: 'Á¶èÂª∫È´òÁ≠âÊ≥ïÈô¢ÈáëÈñÄÂàÜÈô¢', value: 'fjgdfyjmfy' },
        { name: 'Ëá∫ÁÅ£Ëá∫ÂåóÂú∞ÊñπÊ≥ïÈô¢', value: 'twtbdfy' },
        { name: 'Ëá∫ÁÅ£Êñ∞ÂåóÂú∞ÊñπÊ≥ïÈô¢', value: 'twxbdfy' },
        { name: 'Ëá∫ÁÅ£Â£´ÊûóÂú∞ÊñπÊ≥ïÈô¢', value: 'twslgdfy' },
        { name: 'Ëá∫ÁÅ£Ê°ÉÂúíÂú∞ÊñπÊ≥ïÈô¢', value: 'twtydfy' },
        { name: 'Ëá∫ÁÅ£Êñ∞Á´πÂú∞ÊñπÊ≥ïÈô¢', value: 'twxzdfy' },
        { name: 'Ëá∫ÁÅ£ËãóÊ†óÂú∞ÊñπÊ≥ïÈô¢', value: 'twmldfy' },
        { name: 'Ëá∫ÁÅ£Ëá∫‰∏≠Âú∞ÊñπÊ≥ïÈô¢', value: 'twtcdfy' },
        { name: 'Ëá∫ÁÅ£ÂçóÊäïÂú∞ÊñπÊ≥ïÈô¢', value: 'twntdfy' },
        { name: 'Ëá∫ÁÅ£ÂΩ∞ÂåñÂú∞ÊñπÊ≥ïÈô¢', value: 'twzhdfy' },
        { name: 'Ëá∫ÁÅ£Èõ≤ÊûóÂú∞ÊñπÊ≥ïÈô¢', value: 'twyldfy' },
        { name: 'Ëá∫ÁÅ£ÂòâÁæ©Âú∞ÊñπÊ≥ïÈô¢', value: 'twjydfy' },
        { name: 'Ëá∫ÁÅ£Ëá∫ÂçóÂú∞ÊñπÊ≥ïÈô¢', value: 'twtndfy' },
        { name: 'Ëá∫ÁÅ£È´òÈõÑÂú∞ÊñπÊ≥ïÈô¢', value: 'twkxdfy' },
        { name: 'Ëá∫ÁÅ£Ê©ãÈ†≠Âú∞ÊñπÊ≥ïÈô¢', value: 'twqtdfy' },
        { name: 'Ëá∫ÁÅ£Â±èÊù±Âú∞ÊñπÊ≥ïÈô¢', value: 'twptdfy' },
        { name: 'Ëá∫ÁÅ£Ëá∫Êù±Âú∞ÊñπÊ≥ïÈô¢', value: 'twtdgdfy' },
        { name: 'Ëá∫ÁÅ£Ëä±ËìÆÂú∞ÊñπÊ≥ïÈô¢', value: 'twhldfy' },
        { name: 'Ëá∫ÁÅ£ÂÆúËò≠Âú∞ÊñπÊ≥ïÈô¢', value: 'twyldfy' },
        { name: 'Ëá∫ÁÅ£Âü∫ÈöÜÂú∞ÊñπÊ≥ïÈô¢', value: 'twjldfy' },
        { name: 'Ëá∫ÁÅ£ÊæéÊπñÂú∞ÊñπÊ≥ïÈô¢', value: 'twphdfy' },
        { name: 'Á¶èÂª∫ÈáëÈñÄÂú∞ÊñπÊ≥ïÈô¢', value: 'fjjmdfy' },
        { name: 'Á¶èÂª∫ÈÄ£Ê±üÂú∞ÊñπÊ≥ïÈô¢', value: 'fjljdfy' }
      ],
      supremeCourtValues: [],
      districtCourtValues: [],
      searchResults: [],
      resultCount: [],
      pageDetial: {
        "page": 1,
        "size": 10,
        "next_page_url": "null",
        "previous_page_url": "null",
        "total_pages": 8,
        "total": 15
      },
      conditionInfo: [
      ],
      maxTextLength: 250,
      dialogVisible: false,
      dialogText: ''
    };
  },
  created() {
    // Call the method to fetch data when component is created
    this.initParams()
    this.initializeCourt()
    this.fetchData()
  },
  methods: {
    getColumnWidth(name) {
      if (name == 'basic_info') {
        return 'min-width: 260px;'
      }
      else if (name == 'jud_date') {
        return 'min-width: 90px;'
      }
      else if (name == 'case_type') {
        return 'min-width: 125px;'
      }
      else if (name == 'case_num') {
        return 'min-width: 90px;'
      }
      return 'min-width: 200px;'
    },
    openDialog(content) {
      this.dialogVisible = true
      this.dialogText = content
    },
    handleClose() {
      this.dialogVisible = false
    },
    checkQueryEnable(name) {
      if (name == 'court_type') {
        return false
      }
      if (name == 'opinion' ||  name === 'fee' || name === 'sub' || name == 'jud_full') {
        if (this.params[name]) {
          return true
        }
      }
      else {
        return true
      }
      return false
    },
    handlePageSize(size) {
      this.pageDetial.size = size
      this.pageDetial.page = 1
      this.fetchData()
    },
    handlePageChange(page) {
      this.pageDetial.page = page
      this.fetchData()
    },
    formatDate(dateStr) {
      const year = parseInt(dateStr.substring(0, 4), 10);
      const month = parseInt(dateStr.substring(4, 6), 10);
      const day = parseInt(dateStr.substring(6, 8), 10);

      // Convert to Minguo calendar by subtracting 1911 from the Gregorian year
      const minguoYear = year - 1911;

      return `Ê∞ëÂúã${minguoYear}Âπ¥${month}Êúà${day}Êó•`;
    },
    convertDateString(input) {
      // Split the input string into start and end dates
      const [startDate, endDate] = input.split('-');

      // Format both start and end dates
      const formattedStartDate = this.formatDate(startDate);
      const formattedEndDate = this.formatDate(endDate);

      // Combine the formatted dates into the final string
      return `Ëµ∑${formattedStartDate} - ËøÑ${formattedEndDate}`;
    },
    getConditionInfo(info) {
      if (this.searchFields[info[0]]) {
        if (this.searchFields[info[0]].name == "jud_date") {
          return `${this.searchFields[info[0]].type || ''}:  ${this.convertDateString(info[1])}`
        }
        else if (this.searchFields[info[0]].name == "court_type") {
          
          let searchCourts = info[1].split(' ')
          this.selectAllCourts = searchCourts.length == this.courtTypeOptions.length
          this.selectDistrictCourts = this.includesAll(searchCourts, this.districtCourtValues)
          this.selectSupremeCourts = this.includesAll(searchCourts, this.supremeCourtValues)
          if(this.selectAllCourts) {
            return `${this.searchFields[info[0]].type || ''}: ÊâÄÊúâÊ≥ïÈô¢`
          }
          let simplifyCourts = searchCourts
          if(this.selectDistrictCourts) {
            simplifyCourts = simplifyCourts.filter( ( el ) => !this.districtCourtValues.includes( el ) )
            simplifyCourts.unshift('ÊâÄÊúâÂú∞ÊñπÊ≥ïÈô¢')
          }
          if(this.selectSupremeCourts) {
            simplifyCourts = simplifyCourts.filter( ( el ) => !this.supremeCourtValues.includes( el ) )
            simplifyCourts.unshift('ÊâÄÊúâÈ´òÁ≠âÊ≥ïÈô¢')
          }
          return `${this.searchFields[info[0]].type || ''}: ${simplifyCourts.join(', ')}`
        }
        else {
          return `${this.searchFields[info[0]].type || ''}:  ${info[1]}`
        }
        
      }
    },
    addHighlighter(fieldName, rawData) {
      let keywords = null
      this.conditionInfo.forEach((condition) => {
        if (condition[0] == fieldName) {
          keywords = condition[1]
          return
        }
      })

      let result = rawData
      if(keywords) {
        keywords.split(" ").forEach((keyword) => {
        result = result.replace(keyword,`<span class="highlighter-my">${keyword}</span>`)
      })
      }
      result = result.replace(/\n+/g, '<br>')
      // Remove <br> at the end of the string
      result = result.replace(/<br>$/, '')
      return result
    },
    removeEmptyStringValues(obj) {
        Object.keys(obj).forEach(key => {
            if (obj[key] === '') {
                delete obj[key];
            }
        });
        return obj;
    },
    includesAll(arr, values) {
      return values.every(v => arr.includes(v))
    },
    initializeCourt() {
      this.supremeCourtValues = this.courtTypeOptions
        .filter(option => option.name.includes('È´òÁ≠âÊ≥ïÈô¢'))
        .map(option => option.name)

      this.districtCourtValues = this.courtTypeOptions
        .filter(option => option.name.includes('Âú∞ÊñπÊ≥ïÈô¢'))
        .map(option => option.name)
    },
    initParams() {
      const urlParams = new URLSearchParams(window.location.search)

      this.params = {
        'search_method': "keyword",
        'page': 1,
        'size': this.pageDetial.size,
        'court_type': urlParams.get('court_type') || '', 
        'case_type': urlParams.get('case_type') || '',
        'jud_date': urlParams.get('jud_date') || '', 
        'basic_info': urlParams.get('basic_info') || '', 
        'syllabus': urlParams.get('syllabus') || '', 
        'opinion': urlParams.get('opinion') || '', 
        'fee': urlParams.get('fee') || '', 
        'sub': urlParams.get('sub') || '', 
        'jud_full': urlParams.get('jud_full') || ''
      }
      this.params = this.removeEmptyStringValues(this.params)
      // console.log(this.params)
    },
    async fetchData() {
      this.loading = true
      this.params.page = this.pageDetial.page
      this.params.size = this.pageDetial.size
      try {
        const response = await axios.get('https://hssai-verdictdb.phys.nthu.edu.tw/api/search', {
          headers: {
            "ngrok-skip-browser-warning": "69420"
          },
          params: this.params
        });
        // const apiResponse = testSearchResults
        const apiResponse = response.data
        // console.log(apiResponse)
        this.searchResults = apiResponse.data
        this.conditionInfo = apiResponse.condition_info.available
        this.pageDetial = apiResponse.meta
        this.resultCount = apiResponse.summary
        this.loading = false
      } catch (error) {
        console.error('There was an error!', error)
        this.loading = false
      }
    }
  }
};
</script>

